import { reactive } from "vue"
import axios from "./axios"

const auth = reactive({
  isLoggedIn: false,
  isStaff: false,
  isInitialized: false,
  isInitializing: false, // Add flag to prevent multiple initializations

  init() {
    if (this.isInitializing) return // Prevent multiple simultaneous initializations

    this.isInitializing = true
    const isDemoMode = window.location.hostname.includes("github.io") || process.env.VUE_APP_DEMO_MODE === "true"

    if (isDemoMode) {
      // In demo mode, check localStorage for auth state
      this.isLoggedIn = !!localStorage.getItem("access")
      this.isStaff = localStorage.getItem("is_staff") === "true"
    } else {
      // In development mode
      this.isLoggedIn = !!localStorage.getItem("access")
      this.isStaff = localStorage.getItem("is_staff") === "true"
    }

    this.isInitialized = true
    this.isInitializing = false

    // Dispatch auth change event
    window.dispatchEvent(new CustomEvent("auth-changed"))
  },

  async checkTokenValidity() {
    // Skip check in demo mode
    if (process.env.VUE_APP_DEMO_MODE === "true") {
      this.isInitialized = true
      return true
    }

    const token = localStorage.getItem("access")
    if (!token) {
      this.isInitialized = true
      return false
    }

    try {
      await axios.get("user/")
      this.isInitialized = true
      return true
    } catch (error) {
      console.log("Token invalid or expired, logging out")
      this.logout()
      return false
    }
  },

  logout(redirectToGithubPages = false) {
    const cart = localStorage.getItem("cart")
    localStorage.clear()
    if (cart) localStorage.setItem("cart", cart)

    this.isLoggedIn = false
    this.isStaff = false
    this.isInitialized = false
    this.isInitializing = false

    // Dispatch auth change event
    window.dispatchEvent(new CustomEvent("auth-changed"))

    if (redirectToGithubPages) {
      // This will be handled by the component using router
    }
  },

  // Add method to wait for initialization
  async waitForInitialization() {
    return new Promise((resolve) => {
      if (this.isInitialized && !this.isInitializing) {
        resolve()
        return
      }

      const checkInterval = setInterval(() => {
        if (this.isInitialized && !this.isInitializing) {
          clearInterval(checkInterval)
          resolve()
        }
      }, 50)

      // Timeout after 5 seconds to prevent infinite waiting
      setTimeout(() => {
        clearInterval(checkInterval)
        resolve()
      }, 5000)
    })
  },

  reinitialize() {
    this.isInitialized = false
    this.isInitializing = false
    this.init()
  },

  // Method to handle successful login
  handleLoginSuccess(userData) {
    this.isLoggedIn = true
    this.isStaff = userData.is_staff || false
    this.isInitialized = true
    this.isInitializing = false

    // Store auth data
    localStorage.setItem("access", userData.access || "mock-token")
    localStorage.setItem("is_staff", this.isStaff.toString())
    localStorage.setItem("username", userData.username || userData.user?.username || "")

    // Dispatch auth change event
    window.dispatchEvent(new CustomEvent("auth-changed"))
  },
})

// Initialize immediately
auth.init()

export { auth }
