<template>
  <div class="login-container">
    <h1>Login</h1>
    <div v-if="error" class="error-message">{{ error }}</div>
    <form @submit.prevent="login">
      <div class="form-group">
        <label for="username">Username:</label>
        <input type="text" id="username" v-model="username" required>
      </div>
      <div class="form-group">
        <label for="password">Password:</label>
        <input type="password" id="password" v-model="password" required>
      </div>
      <button type="submit" :disabled="isLoading">
        <span v-if="isLoading">Logging in...</span>
        <span v-else>Login</span>
      </button>
    </form>
    <div class="notification" :class="notificationType" v-if="notificationMessage">
      {{ notificationMessage }}
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      username: '',
      password: '',
      isLoading: false,
      error: '',
      notificationMessage: '',
      notificationType: ''
    };
  },
  methods: {
    async login() {
      if (this.isLoading) return
      
      this.isLoading = true
      this.error = ''
      
      try {
        const response = await axios.post('token/', {
          username: this.username,
          password: this.password
        })
        
        const userData = response.data
        
        // Use the new auth method to handle login success
        this.$auth.handleLoginSuccess({
          access: userData.access,
          username: userData.user?.username || this.username,
          is_staff: userData.user?.is_staff || false
        })
        
        this.showNotification('success', 'Login successful!')
        
        // Navigate based on user type
        if (this.$auth.isStaff) {
          this.$router.push('/monitor')
        } else {
          this.$router.push('/products')
        }
        
      } catch (error) {
        console.error('Login error:', error)
        if (error.response?.data?.detail) {
          this.error = error.response.data.detail
        } else {
          this.error = 'Login failed. Please try again.'
        }
        this.showNotification('error', this.error)
      } finally {
        this.isLoading = false
      }
    },
    showNotification(type, message) {
      this.notificationType = type;
      this.notificationMessage = message;
      setTimeout(() => {
        this.notificationMessage = '';
        this.notificationType = '';
      }, 3000); // Clear after 3 seconds
    }
  }
};
</script>

